.footer {
    width: 100%;
}

.footer-content {
        padding: 50px 20px;
        max-width: 1200px;
        margin: 0 auto;
        display: flex;
        flex-flow: wrap;
        gap: 20px;
}

footer-content-column-1{
    max-width: 300px;
}

.title{
    margin-bottom: 20px;
    font-size: 20px;
}
.text{
    color: rgba(0,0,0,0.5);
    font-size: 14px;
}

.c-item{
    display: flex;
    margin-bottom: 10px;
}

.text{
    flex-shrink: 0;
    font-size: 14px;
    margin-right: 10px;
    margin-top: 4px;
    color: rgba(0, 0, 0, 0.5);
}


.bottom-bar{
    border-top: 2px solid rgba(0,0,0,0.1);
}

.bottom-bar-content{
    padding:20px;
    display: flex;
    align-items: center;
    flex-direction: column;
    text-align: center;
    gap: 10px;
}

.bottom-bar-text{
    font-size: 12px;
    color: rgba(0,0,0,0.5);
}

