.stories_cover{
    /* padding: 40px 0; */
    position: relative;
    /* background-image: linear-gradient(to right, #8e2de2, #4a00e0); */
    background-image: url("../../assets/storiesCover.jpg");
    background-size: cover;
    background-repeat: no-repeat;
    min-height: 35vh;
    height: 100px;
    background-position: center;
    cursor: pointer;
    /* padding-top: 4rem; */
}

.profile-img{
    width: 180px;
    height: 180px;
    border-radius: 50%;
    padding: -80px -50px;
    position: relative;
    cursor: pointer;
    margin-top: -25%;
    border: 5px solid transparent;
    /* background:linear-gradient(45deg,red,blue); */
    background: linear-gradient(120deg, #ffc300, #1e90ff); 
    border-image: linear-gradient(120deg, #ffc300, #1e90ff) 1;
    transition: transform .3s;
}

.profile-img:hover{
    transform: scale(1.1);
}

/* ========================== */
.cta{
    margin-top: 2.5rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;
}

.cta > div {
    flex-basis: 100%;
    width: 0;
}
.cta .button{
    margin: 16px;
}
@media (max-width: 400px) {
    .cta .button {
      margin: 12px;
    }
  }


  /* =========================== */

.profile-sections{
    display: flex;
    gap: 50px;
    margin-left: 8%;
    
}

.profile-section{
    display: flex;
    gap: 50px;
    margin-left: 8%;
    
}

.circles{
    display: flex;
    margin-left: 20%;
    gap: 30%;
    align-items: center;
    flex-direction: row-reverse;
}

.circle1{
    width: 150px;
    height: 150px;
    flex-shrink: 0;
    border-radius: 150px;
    background: #003566;
    filter: blur(50px);
    display: flex;
    align-items:flex-end;
    margin-top: -10%;
}
.circle2{
    width: 50px;
    height: 50px;
    flex-shrink: 0;
    border-radius: 50px;
    background: #ffc300;
    filter: blur(20px);
    display: flex;
    align-items:flex-end;
    margin-top: 30%;
}

.profile-details{
    position: relative;
    margin-top: 10px;
}

.profile-name{
    font-size: 250%;
    font-weight: bold;
}

.follow-btn{
    display: flex;
    gap: 15px;
    margin-left: 20px;
    /* transition: transform .3s; */
}
#btn1 , #btn2{
    transition: transform .3s;
}
#btn1:hover, #btn2:hover{
    transform: scale(1.1);
}

/* .follow-btn:hover{
    transform: scale(1.1);
} */
button{
    width: 100px;
    height: 45px;
    border-radius: 10px;
    border: 2px solid gray;
    cursor: pointer;
    font-size: 20px;
    color: black;
    background-color: #ece7e7
}

.follow-txt{
    display: flex;
    gap: 11%;
    margin-left: 9%;
    margin-top: -15px;
    color: gray;
}

/* .follow-txt:hover{
    transform: scale(1.1);
} */

.profile-post{
    font-size: 160%;
}

.insta-link{
    text-decoration: none;
    color: #00b4d8;
    font-size: 120%;
}

.profile-position{
    margin-left: 9%;
    margin-top: 1.5%;
    
}

.profile-data{
    display: flex;
    align-items: center;
    gap: 2%;
    font-size: 130%;
    margin-top: 1.5%;
}

span{
    margin-right: 15px;
    justify-content: space-between;
    gap: 20px;
}

#data1,#data2,#data3,#data4{
    transition: transform .3s;
}

#data1:hover,#data2:hover,#data3:hover,#data4:hover{
    transform: scale(1.4);
}





/* =============================================== */

.tabs:not(:last-child){
    border-right: 1px solid transparent;
}

.active-tabs{
    background: white;
    border-bottom: 1px solid transparent;
    font-size: 50px;
}

.active-tabs::before{
    content: '';
    display: block;
    position: absolute;
    top: -5px;
    left: 50%;
    transform: translateX(-50%);
    width: calc(100% + 2px);
    height: 5px;
    background: rgba(88,147,241);
}
