nav{
    width: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    background-color: black;
    /* z-index: 99; */
}

.nav-items{
    display: flex;
    align-items: center;
    gap: 10px;
}

&.sticky-header{
    /* position: sticky; */
    top: 0px;
    transform: translateY(-80px);
    animation: stickyHeader 0.3s ease forwards;

}

img{
    width: 60px;
    height: 55px;
    margin: 5px 5px 5px 15px;
    transition: transform .3s;
}

.logo{
    transition: transform .3s;
}

.logo:hover{
    transform: scale(1.1);
}

.nav-btns{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    text-align: center;
}

.btn1{
    font-size: 30px;
    letter-spacing: 8px;
    font-family: 'Poppins', sans-serif;
    transition: transform .3s;
}

.btn1:hover{
    transform: scale(1.1);
}

.btn2{
    width: 110px;
    height: 50px;
    border-radius: 10px;
    border: solid #f08c19;
    text-align: center;
    font-size: 25px;
    font-weight: 600px;
    background-color: #FFB703;
    font-family: 'Poppins', sans-serif;
    transition: transform .3s;
    
}

.btn2:hover{
    transform: scale(1.1);
}